.reviews {
  padding-top: 40px;
  padding-bottom: 56px;
}

.reviews__icons {
  display: none;
}

.reviews__list {
  margin-bottom: 30px;
}

.reviews__title {
  margin: 0;
  padding-bottom: 13px;

  line-height: 30px;
}

.reviews__text {
  margin: 0;
  font-family: $ff-main;
  font-size: $fz-main;
  line-height: 30px;
  color: $c-main;
  text-align: center;
}

.reviews__toggles {
  bottom: -43px;
}

.reviews__description {
  display: none;
}

@media (min-width: $tablet-width) {
  .reviews {
    padding-top: 0;
    padding-bottom: 0;
  }

  .reviews__list {
    margin-bottom: 35px;
  }

  .reviews__text {
    font-size: 18px;
    padding-bottom: 35px;
  }

  .reviews__title {
    font-size: 20px;
    padding-bottom: 0;
  }

  .reviews__item {
    display: flex;
    flex-direction: column;
  }

  .reviews__title-wrap {
    order: 1;
  }

  .reviews__description {
    display: block;
    margin: 0;
    font-family: $ff-main;
    font-size: $fz-main;
    line-height: 30px;
    color: $c-main;
    text-align: center;
  }
}

@media (min-width: $desktop-width) {
  .reviews {
    padding-top: 71px;
    padding-bottom: 0;
  }

  .reviews__icons {
    display: flex;
    justify-content: center;
    position: relative;
    padding-bottom: 50px;
  }

  .icons__quotes {
    fill: $special-color-quotes;
  }

  .icons__left,
  .icons__right {
    position: absolute;
    top: 142px;
    fill: $c-main-toggle;

    &:hover {
      fill: $c-main-toggleOpacity;
    }

    &:active {
      fill: $c-main-opacity;
    }
  }

  .icons__left {
    left: 130px;
  }

  .icons__right {
    right: 130px;
  }

  .reviews__title {
    padding-bottom: 3px;
    font-size: 24px;
  }

  .reviews__description {
    font-size: 18px;
  }

  .reviews__text {
    display: inline-block;
    width: 688px;
    margin: 0 auto;
  }
}
