* {
	margin: 0;
	padding: 0;
}

.slideshow-wrap {
	display: block;
	height: 242px;
	min-width: 320px;
	max-width: 940px;
	margin: auto;
  position: relative;
  padding: 0 20px;
}

.slideshow-inner {
	width: 100%;
	height: 100%;
	overflow: hidden;
	position: relative;
}

.slider1,
.slider2 {
	list-style: none;
	height: 100%;
	width: 500%;
	overflow: hidden;
	position: relative;
  left: 0px;
  display: flex;
	transition: left .8s cubic-bezier(0.77, 0, 0.175, 1);
}

.slides {
	width: 20%;
	height: 200px;
  position: relative;
  display: flex;
  flex-direction: column;
}

.slideshow-wrap input[type=radio] {
	position: absolute;
	left: 50%;
	bottom: 15px;
	z-index: 100;
	visibility: hidden;
}

.slideshow-wrap label:not(.arrows) {
	position: absolute;
	left: 50%;
	bottom: -20px;
	z-index: 100;
	width: 6px;
	height: 6px;
  background-color: $c-main-toggle;
	border-radius: 50%;
	cursor: pointer;
	transition: background-color .2s;
}

.slideshow-wrap label:not(.arrows):active {
  bottom: -23px;
}

.slideshow-wrap input[type=radio]#button-1:checked~label[for=button-1]:not(.arrows) {
  width: 12px;
  height: 12px;
  background-color: transparent;
  border: 3px solid $c-main;
  bottom: -23px;
}

.slideshow-wrap input[type=radio]#button-2:checked~label[for=button-2]:not(.arrows) {
  width: 12px;
  height: 12px;
  background-color: transparent;
  border: 3px solid $c-main;
  bottom: -23px;
  margin-left: -6px;
}

.slideshow-wrap input[type=radio]#button-3:checked~label[for=button-3]:not(.arrows) {
  width: 12px;
  height: 12px;
  background-color: transparent;
  border: 3px solid $c-main;
  bottom: -23px;
}

.slideshow-wrap input[type=radio]#button-4:checked~label[for=button-4] {
  width: 12px;
  height: 12px;
  background-color: transparent;
  border: 3px solid $c-main;
  bottom: 0;
}

.slideshow-wrap input[type=radio]#button-5:checked~label[for=button-5] {
  width: 12px;
  height: 12px;
  background-color: transparent;
  border: 3px solid $c-main;
  bottom: 0;
  margin-left: -6px;
}

.slideshow-wrap input[type=radio]#button-6:checked~label[for=button-6] {
  width: 12px;
  height: 12px;
  background-color: transparent;
  border: 3px solid $c-main;
  bottom: 0;
}

